, .5);\n}\n/*-----------------------\n5.2.  Data Tables\n--------------------*/\n/*-- row group table */\ntr.group, tr.group:hover {\n    background-color: #5969ff !important;\n    color: #fff;\n    font-family: 'Roboto';\n    font-size: 18px;\n}\n/*-----------------------\n5.3. Table Filters\n--------------------*/\n/* ==========================================================================\n 6. Pages\n ========================================================================== */\n/*-----------------------\n6.1 Blank Page\n-------------------------*/\n/*-----------------------\n6.2 Blank Page Header\n-------------------------*/\n/*-----------------------\n6.3 Login\n-------------------------*/\n/*-----------------------\n6.4 Sign up Page\n-------------------------*/\n/*-----------------------\n6.5 Forgot Password\n-------------------------*/\n/*-----------------------\n6.6 Profile\n-------------------------*/\n/*-----------------------\n6.7 Pricing\n-------------------------*/\n/*-----------------------\n6.8 Timeline\n-------------------------*/\n/* --------------------------------\n\nPatterns - reusable parts of our design\n\n-------------------------------- */\n@media only screen and (min-width:1170px) {\n    .cd-is-hidden {\n        visibility: hidden;\n    }\n}\n/* --------------------------------\n\nVertical Timeline - by CodyHouse.co\n\n-------------------------------- */\n.cd-timeline {\n    overflow: hidden;\n    margin: 2em auto;\n}\n.cd-timeline__container {\n    position: relative;\n    width: 90%;\n    max-width: 1170px;\n    margin: 0 auto;\n    padding: 2em 0;\n}\n.cd-timeline__container::before {\n    /* this is the vertical line */\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 18px;\n    height: 100%;\n    width: 4px;\n    background: #dedee9;\n}\n@media only screen and (min-width:1170px) {\n    .cd-timeline {\n        margin-top: 3em;\n        margin-bottom: 3em;\n    }\n    .cd-timeline__container::before {\n        left: 50%;\n        margin-left: -2px;\n    }\n}\n.cd-timeline__block {\n    position: relative;\n    margin: 2em 0;\n}\n.cd-timeline__block:after {\n    /* clearfix */\n    content: \"\";\n    display: table;\n    clear: both;\n}\n.cd-timeline__block:first-child {\n    margin-top: 0;\n}\n.cd-timeline__block:last-child {\n    margin-bottom: 0;\n}\n@media only screen and (min-width:1170px) {\n    .cd-timeline__block {\n        margin: 4em 0;\n    }\n}\n.cd-timeline__img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 40px;\n    height: 40px;\n    border-radius: 50%;\n    box-shadow: 0 0 0 4px white, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);\n}\n.cd-timeline__img img {\n    display: block;\n    width: 24px;\n    height: 24px;\n    position: relative;\n    left: 50%;\n    top: 50%;\n    margin-left: -12px;\n    margin-top: -12px;\n}\n.cd-timeline__img.cd-timeline__img--picture {\n    background: #2ec551;\n}\n.cd-timeline__img.cd-timeline__img--movie {\n    background: #ef172c;\n}\n.cd-timeline__img.cd-timeline__img--location {\n    background: #ffc108;\n}\n@media only screen and (min-width:1170px) {\n    .cd-timeline__img {\n        width: 60px;\n        height: 60px;\n        left: 50%;\n        margin-left: -30px;\n        /* Force Hardware Acceleration */\n        -webkit-transform: translateZ(0);\n        transform: translateZ(0);\n    }\n    .cd-timeline__img.cd-timeline__img--bounce-in {\n        visibility: visible;\n        -webkit-animation: cd-bounce-1 0.6s;\n        animation: cd-bounce-1 0.6s;\n    }\n}\n@-webkit-keyframes cd-bounce-1 {\n    0% {\n        opacity: 0;\n        -webkit-transform: scale(0.5);\n        transform: scale(0.5);\n    }\n    60% {\n        opacity: 1;\n        -webkit-transform: scale(1.2);\n        transform: scale(1.2);\n    }\n    100% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n}\n@keyframes cd-bounce-1 {\n    0% {\n        opacity: 0;\n        -webkit-transform: scale(0.5);\n        transform: scale(0.5);\n    }\n    60% {\n        opacity: 1;\n        -webkit-transform: scale(1.2);\n        transform: scale(1.2);\n    }\n    100% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n}\n.cd-timeline__content {\n    position: relative;\n    margin-left: 60px;\n    background: white;\n    border-radius: 0.25em;\n    padding: 1em;\n    border: none;\n    box-shadow: 0px 1px 2px 1px rgba(154, 154, 204, 0.22);\n}\n.cd-timeline__content:after {\n    /* clearfix */\n    content: \"\";\n    display: table;\n    clear: both;\n}\n.cd-timeline__content::before {\n    /* triangle next to content block */\n    content: '';\n    position: absolute;\n    top: 16px;\n    right: 100%;\n    height: 0;\n    width: 0;\n    border: 7px solid transparent;\n    border-right: 7px solid white;\n}\n.cd-timeline__read-more, .cd-timeline__date {\n    display: inline-block;\n}\n.cd-timeline__read-more {\n    float: right;\n    padding: .8em 1em;\n    background: #acb7c0;\n    color: white;\n    border-radius: 0.25em;\n}\n.cd-timeline__read-more:hover {\n    background-color: #bac4cb;\n}\n.cd-timeline__date {\n    float: left;\n    padding: .8em 0;\n    opacity: .7;\n}\n@media only screen and (min-width:768px) {\n\n\n\n}\n@media only screen and (min-width:1170px) {\n    .cd-timeline__content {\n        margin-left: 0;\n        padding: 1.6em;\n        width: 45%;\n        /* Force Hardware Acceleration */\n        -webkit-transform: translateZ(0);\n        transform: translateZ(0);\n    }\n    .cd-timeline__content::before {\n        top: 24px;\n        left: 100%;\n        border-color: transparent;\n        border-left-color: white;\n    }\n    .cd-timeline__read-more {\n        float: left;\n    }\n    .cd-timeline__date {\n        position: absolute;\n        width: 100%;\n        left: 122%;\n        top: 6px;\n        font-size: 18px;\n    }\n    .cd-timeline__block:nth-child(even) .cd-timeline__content {\n        float: right;\n    }\n    .cd-timeline__block:nth-child(even) .cd-timeline__content::before {\n        top: 24px;\n        left: auto;\n        right: 100%;\n        border-color: transparent;\n        border-right-color: white;\n    }\n    .cd-timeline__block:nth-child(even) .cd-timeline__read-more {\n        float: right;\n    }\n    .cd-timeline__block:nth-child(even) .cd-timeline__date {\n        left: auto;\n        right: 122%;\n        text-align: right;\n    }\n    .cd-timeline__content.cd-timeline__content--bounce-in {\n        visibility: visible;\n        -webkit-animation: cd-bounce-2 0.6s;\n        animation: cd-bounce-2 0.6s;\n    }\n}\n@media only screen and (min-width:1170px) {\n    /* inverse bounce effect on even content blocks */\n    .cd-timeline__block:nth-child(even) .cd-timeline__content.cd-timeline__content--bounce-in {\n        -webkit-animation: cd-bounce-2-inverse 0.6s;\n        animation: cd-bounce-2-inverse 0.6s;\n    }\n}\n@-webkit-keyframes cd-bounce-2 {\n    0% {\n        opacity: 0;\n        -webkit-transform: translateX(-100px);\n        transform: translateX(-100px);\n    }\n    60% {\n        opacity: 1;\n        -webkit-transform: translateX(20px);\n        transform: translateX(20px);\n    }\n    100% {\n        -webkit-transform: translateX(0);\n        transform: translateX(0);\n    }\n}\n@keyframes cd-bounce-2 {\n    0% {\n        opacity: 0;\n        -webkit-transform: translateX(-100px);\n        transform: translateX(-100px);\n    }\n    60% {\n        opacity: 1;\n        -webkit-transform: translateX(20px);\n        transform: translateX(20px);\n    }\n    100% {\n        -webkit-transform: translateX(0);\n        transform: translateX(0);\n    }\n}\n@-webkit-keyframes cd-bounce-2-inverse {\n    0% {\n        opacity: 0;\n        -webkit-transform: translateX(100px);\n        transform: translateX(100px);\n    }\n    60% {\n        opacity: 1;\n        -webkit-transform: translateX(-20px);\n        transform: translateX(-20px);\n    }\n    100% {\n        -webkit-transform: translateX(0);\n        transform: translateX(0);\n    }\n}\n@keyframes cd-bounce-2-inverse {\n    0% {\n        opacity: 0;\n        -webkit-transform: translateX(100px);\n        transform: translateX(100px);\n    }\n    60% {\n        opacity: 1;\n        -webkit-transform: translateX(-20px);\n        transform: translateX(-20px);\n    }\n    100% {\n        -webkit-transform: translateX(0);\n        transform: translateX(0);\n    }\n}\n/*-----------------------\n6.9 404 error\n-------------------------*/\n.error-section {\n    padding: 74px 0px;\n}\n.error-section-content {\n    padding-top: 30px;\n    color: #3d405c;\n    padding-bottom: 30px;\n}\n/*-----------------------\n6.9 Calendar\n-------------------------*/\n#calendar1 {\n    margin: 0 auto;\n}\n#wrap {\n    /* width: 1100px; */\n    margin: 0 auto;\n}\n#external-events {\n    float: left;\n    width: 270px;\n    padding: 0 20px;\n    border: 1px solid #e0e4ef;\n    background: #f9f9ff;\n    text-align: left;\n    border-radius: 4px;\n}\n#external-events h4 {\n    font-size: 16px;\n    margin-top: 0;\n    padding-top: 1em;\n}\n#external-events .fc-event {\n    margin: 10px 0;\n    cursor: pointer;\n}\n#external-events p {\n    margin: 1.5em 0;\n    font-size: 11px;\n    color: #666;\n}\n#external-events p input {\n    margin: 0;\n    vertical-align: middle;\n}\n#calendar {\n    float: right;\n    width: 1240px;\n}\n/*-----------------------\n6.10 Metrics\n-------------------------*/\n.metric-row {\n    margin-bottom: 1.25rem;\n    border-radius: .25rem;\n    align-items: stretch;\n    display: flex;\n    flex-wrap: wrap;\n    margin-right: -10px;\n    margin-left: -10px;\n}\n.metric-value {\n    margin-bottom: 0;\n    line-height: 1;\n    white-space: nowrap;\n}\n.metric-label {\n    font-size: .875rem;\n    font-weight: 500;\n    color: #686f76;\n    white-space: nowrap;\n}\n.metric-label:last-child {\n    margin-top: .5rem;\n    margin-bottom: 0;\n}\n.